@import url('https://fonts.googleapis.com/css?family=Roboto');

body{
    font-size: 16px;
}



#nav-menu{
    border: 1px solid red;
    display: flex;
    justify-content: space-between;
    width: 80%;
    height: 80px;
    border-radius: 50px;
    position: fixed;
    left:10%;
    top:2%;
    margin-bottom: 100px;

    




}




.logo {
  
    border-radius: 50%;
    width: 80px;
    min-width: 10px;
    max-width: 10%;
    height:80px;
    max-height: auto;
    margin-left: 0px;

}
.logo img{
    height: 75px;
    width: 75px;
    border-radius: 50%;
   border: 2px solid blueviolet;

}


.logo a {
    text-decoration: none;
    
   
  
}


 .logo a:hover {
   
}


.main-list{
    margin-right: 1%;
    width: 70%;
    /* padding: 10px; */


}
.main-list  button{
     border-radius: 50%;
     height: auto;
     

     
}



#nav-menu ul>li{
     list-style: none;
     

}

#nav-menu ul>li>a{
     text-decoration: none;
     color: #000;
    
}

#nav-menu>div{
     
     /* border: 1px solid black; */
     margin-right: 32px;
}


.ul-links{
    display: flex;
    justify-content: space-between;
      align-items: center; 
      font-size: 16px;
      margin-top: 25px;
      flex-wrap: wrap;
      
 
    
}

#dark-mode-btn{
     font-size: 20px;

}

#burger-menu{
    display: none;

}

#ho,#ab,#sk,#co,#pr,#resume-link-1,#st{
    display: flex;
    align-items: center;
}
#ho:hover,#ab:hover,#sk:hover,#co:hover,#pr:hover,#re:hover,#st:hover{
 
    border-bottom:2px solid black;
    transition:border-bottom 0.1s ease;
}



@media  (min-width:300px) and (max-width:900px){
   
 #burger-menu{
        display: block;

     }
     .ul-links .nav-icons{
        display: none;
     }
}