.skills-section{

    border: 1px solid green;
    width: 75%;
    margin: auto;
    padding: 10px;
    margin-top: 10px;


}
.skills-section>h1{
    font-family: 'Chela One', cursive;
    word-spacing: 5px;
    letter-spacing: 2px;
    margin-top: 100px;
}

.skills-card{
    border: 1px solid red;
    height: auto;
    display: grid;
    grid-template-columns: repeat(5,1fr);
    grid-template-rows: auto;
    gap: 80px;
    padding: 10px;


    
}
.skills-card >div{
    /* border: 1px solid black; */
    height: 140px;
    width: 140px;
}

.card1,.card2,.card3,.card4,.card5,.card6,.card7,.card8,.card9,.card10{

   
 display: flex;
 justify-content: space-around;
 align-items: center;
     
}

