


.home-section{

    /* border: 1px solid blue; */
    min-height: 500px;
    max-height:auto ;

    width: 75%;
    margin: auto;
    margin-top: 100px;
    padding: 10px;
   

}

.home-div{
     /* border: 1px solid red; */
     height: inherit;
     margin: auto;
     display: flex;
    justify-content: space-between;
    flex-wrap: wrap;



}
.home-div >div{

    margin: 10px;
    margin-top: 60px;


}
.home-imgee{
     /* border: 1px solid green; */
     height: 400px;
     margin-top: 10px;
     width: 40%;
     

     
     /* border-radius: 50%; */
     
}
.home-imgee>div{
    /* border: 1px solid brown; */
    
    width: 80%;
   
    margin: auto;    
    margin-top: 50px;

}
.home-imgee  img{
    width: inherit;
    height: inherit;
    border-radius: 50%;
    border: 2px solid blueviolet;
    margin-left: 30px;
    

}
.home-details{
    /* border: 1px solid green; */
    width: 50%;
    height: 400px;
   
}
.home-details>h1{
    font-size: 2em;    
    word-spacing: 5px;
    letter-spacing: 2px;
    font-family: 'Chela One', cursive;
    margin-top: 50px;

    
}

.welcome{
    color: rgb(228, 131, 131);
}


#user-detail{
    font-size: 1rem;
}
h2{
    margin-top: 30px;
}

.social-media{
    /* border: 1px solid orange; */
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 15px;
    margin-top: 30px;
   

}

.social-media>div{.home-details>span{
    font-size: 20px;
    font-family:'Chela One', cursive; 
